    componentDidMount() {
        this.setState({
            filtered: this.state.films
        });

    }

    componentDidUpdate() {
        console.log(this.state.favorites)
    }


    onChangeHandler = (e) => {
        const {films, filtered} = this.state;
        const value = e.target.value;
        let newArray = [];
        let currentList = [];

        if (value !== '') {
            currentList = films;
            newArray = currentList.filter((item) => {
                return item.title.toUpperCase().indexOf(value.toUpperCase()) !== -1
            });
            //console.log(newArray);

        } else {
            console.log('empty srtring');
            newArray = films;
        }
        this.setState({
            filtered: newArray
        })
    };



    // showBookmarks = (e) => {
    //     const {bookmarksActive} = this.state;
